<template>
  <header>


    <el-menu
      :default-active="activeIndex"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
    >
      <el-menu-item class="nav-brand">
        <i class="el-icon-food"></i>
        <b>FOODAPP</b>
      </el-menu-item>
      <el-menu-item index="1"><router-link :to="{name:'homeLink'}">Home</router-link></el-menu-item>
      <el-menu-item index="2"><router-link :to="{name:'menuLink'}">Menu</router-link></el-menu-item>
      <el-menu-item index="3"><router-link :to="{name:'manageLink'}">Manage</router-link></el-menu-item>
      <el-menu-item index="4"><router-link :to="{name:'aboutLink'}">About</router-link></el-menu-item>
     
      <div class="nav-right">
        <el-menu-item index="5" class="nav-item"><router-link to="/login">Login</router-link></el-menu-item>
        <el-menu-item index="6" class="nav-item"><router-link to="/register">Register</router-link></el-menu-item>
 </div>

    </el-menu>

  </header>
</template>


<script>
export default {
  data() {
    return {
      
      activeIndex: "1",
      activeIndex2: "1"
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  }
};
</script>


<style>
.nav-right {
  position: relative;
  float: right;
}
.nav-item {
  display: inline-block;
  
}

/* {
    width: 800px;
    margin: 0 auto;
} */
</style>



