<template>
  <el-card class="box-card">
    <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
      <el-form-item label="ID">
        <el-input v-model="formLabelAlign.name"></el-input>
      </el-form-item>
      <el-form-item label="Password">
        <el-input type="password" v-model="formLabelAlign.password"></el-input>
      </el-form-item>
      <div class="btn">
        <el-button type="primary" size:small>Login in</el-button>
      </div>
    </el-form>
  </el-card>
</template>
<script>
export default {
  data() {
    return {
      labelPosition: "top",
      formLabelAlign: {
        name: "",
        password: ""
      }
    };
  }
};
</script>


<style>
.container {
  padding: 20px;
  width: 400px;
  margin: 0 auto;
}

.el-input {
  width: 355px;
}
.btn {
  float: right;
  margin-bottom: 20px;
}
</style>



