<template>
  <div id="home">
    <div class="background">
      <h1>Welcome</h1>
      <h2>Enjoy it!</h2>
      <div>
        <el-button @click="goToMenu" type="primary" class="btn">Let's Order</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {};
  },
  methods: {
    goToMenu() {
      // this.$router.go(-1);
      // this.$router.replace('/menu');
      // this.$router.replace({name:'menuLink'});
      this.$router.push("/menu");
    }
  }
};
</script>

<style scoped>
#home {
  background: url("../../src/assets/homebg.jpg") no-repeat;
  background-size: cover;
  height: 340px;
  border: black solid;
}
h1,
h2 {
  text-align: center;
  margin: 4%;
}
.btn {
  float: right;
  margin-right: 50px;
}
.background {
  background: #eee;
  opacity: 0.8;

  margin: 0 auto;
  margin-top: 55px;
  padding-top: 20px;
  padding-bottom: 60px;
}
</style>





